#footer .footer-top .footer-newsletter input[type="submit"]:hover {
    background: #0062cc;
}

#footer .copyright {
    text-align: center;
    padding-top: 30px;
}

#footer .credits{
    text-align: center;
    font-size: 13px;
    color: #f1f7ff;
}

#footer .credits a{
    color: #bfddfe;
}

#footer .credits a:hover{
    color: #f1f7ff;
}
